import NavBar from '#app/layouts/navbar/navbar.vue';
import Form from '#app/components/form/form.vue';
import { get as getConfig } from '#helpers/api/config';
import { create } from '#helpers/api/plan';
import { search } from '#helpers/api/operator';

export default {
    components: {
        NavBar,
        Form,
    },

    data() {
        const { plan_types: planTypes, departements, user: { departement } } = getConfig();

        const data = {
            formData: {
                departement,
            },
        };

        data.formDefinition = {
            title: 'Dclaration d\'un dispositif',
            description: 'Ce formulaire vous permet de dclarer un dispositif ou une action mene sur votre territoire. Il peut s\'agir notamment d\'actions d\'accompagnement social global, d\'espaces temporaires d\'insertion ou encore d\'actions sanitaires (dploys sur un ou plusieurs bidonville ou "hors les murs"). Une fois ce formulaire valid, ledispositif fera l\'objet d\'une fiche ddie, accessible via l\'onglet "liste des dispositifs". Lorsque le symbole (*) apparat en rouge au dbut de l\'intitul de la question, cela signifie qu\'il s\'agit d\'une question obligatoire. Il n\'est pas possible de valider le formulaire sans y avoir rpondu.',
            steps: [
                {
                    title: 'Dispositf',
                    wording: {
                        error: 'La dclaration du dispositif a chou',
                        submit: 'Dclarer le dispositif',
                    },
                    sections: [
                        {
                            title: 'Caractristiques gnrales',
                            inputs: {
                                name: {
                                    type: 'text',
                                    label: 'Nom du dispositif',
                                    mandatory: false,
                                },
                                type: {
                                    type: 'radio',
                                    label: 'Type de dispositif',
                                    mandatory: true,
                                    options: planTypes.map(({ id, label }) => ({
                                        value: id,
                                        label,
                                    })),
                                },
                                startedAt: {
                                    type: 'date',
                                    label: 'Date de dbut du dispositif',
                                    mandatory: true,
                                },
                            },
                        },
                        {
                            title: 'Oprateur en charge',
                            inputs: {
                                ngo: {
                                    type: 'autocompleter',
                                    specificProps: {
                                        autocompleter: (d) => {
                                            const p = search(d);
                                            const p2 = p.then(items => items.map(item => ({
                                                id: item.ngo_id,
                                                label: item.label,
                                            })));
                                            p2.abort = p.abort;

                                            return p2;
                                        },
                                        showCategory: false,
                                        allowMultiple: false,
                                        float: true,
                                        createNew: () => {
                                            const routerData = this.$router.resolve('/nouvel-operateur');
                                            window.open(routerData.href, '__blank');
                                        },
                                    },
                                    label: 'Oprateur',
                                    description: 'Commencez  saisir le nom de l\'oprateur, puis slectionnez l\'oprateur dsir dans la liste propose',
                                    mandatory: true,
                                },
                            },
                        },
                        {
                            title: 'Sites concerns',
                            inputs: {
                                departement: {
                                    type: 'select',
                                    label: 'Dpartement concern',
                                    mandatory: true,
                                    options: departements.map(({ code, name }) => ({
                                        value: code,
                                        label: `${code} - ${name}`,
                                    })),
                                },
                                targetedOnTowns: {
                                    type: 'radio',
                                    label: 'L\'action est mene sur un ou plusieurs site(s) en particulier :',
                                    mandatory: true,
                                    options: [
                                        { value: false, label: 'Non' },
                                        { value: true, label: 'Oui' },
                                    ],
                                },
                                towns: {
                                    type: 'townList',
                                    label: 'Sites concerns',
                                    mandatory: false,
                                    specificProps: {
                                        filter: ({ departement: { code } }) => code === this.formData.departement,
                                    },
                                    condition({ targetedOnTowns }) {
                                        return targetedOnTowns;
                                    },
                                },
                            },
                        },
                        {
                            title: 'Financements',
                            inputs: {
                                funding: {
                                    type: 'planFunding',
                                    label: 'Financements 2019',
                                    description: 'Ce tableau de financement ne concerne que l\'anne courante',
                                    mandatory: false,
                                },
                            },
                        },
                    ],
                    submit: d => create(Object.assign({}, d, {
                        ngo: d.ngo && d.ngo.length ? d.ngo[0].id : null,
                    })),
                },
            ],
        };

        return data;
    },

    watch: {
        'formData.departement': function a() {
            this.$refs.form.getInputById('towns').specificProps.filter = ({ departement: { code } }) => code === this.formData.departement;
        },
    },

    methods: {
        onComplete() {
            this.$notify({
                group: 'notifications',
                type: 'success',
                title: 'Dispositif correctement dclar',
                text: 'Le dispositif a bien t ajout en base de donnes',
            });

            this.$router.push('/liste-des-dispositifs');
        },
    },
};
